export default async function SendRQ(url, options = {}) {
  const base_url = "http://104.248.142.206:8000/api/";

  const access = window.localStorage.getItem("ACCESS");
  const headers = options.headers || {};

  if (access) {
    headers["Authorization"] = `Bearer ${access}`;
  }

  let response = await fetch(`${base_url}${url}`, options);

  if (response.status === 401) {
    const refresh = window.localStorage.getItem("REFRESH");
    const refresh_response = await fetch(`${base_url}token/refresh/`, {
      method: "POST",
      headers: {
        "Content-Type": "Application/json",
      },
      body: JSON.stringify({
        refresh,
      }),
    });

    const refresh_data = await refresh_response.json();

    window.localStorage.setItem("ACCESS", refresh_data.access);
    headers["Authorization"] = `Bearer ${refresh_data.access}`;

    response = await fetch(`${base_url}${url}`, {
      headers,
    });
  }

  const data = await response.json();

  return data;
}
