import "./App.js";
import React from "react";
import SendRQ from "./services/ApiClientServer.js";
import { NavLink } from "react-router-dom";

export default function ContractsList() {
  const [role, setRole] = React.useState("blogger");
  const [isLogin] = React.useState(window.localStorage.getItem("ACCESS"));

  const newContractHandler = async (event) => {
    SendRQ("contracts/", {
      headers: {
        "Content-Type": "Application/json",
      },
      method: "POST",
      body: JSON.stringify({
        id: -1,
      }),
    }).then((resp) => {
      window.location.reload();
    });
  };

  React.useEffect(() => {
    SendRQ("whoami/", {
      headers: {
        "Content-Type": "Application/json",
      },
      method: "GET",
    }).then((data) => {
      setRole(data.role);
    });
  }, []);

  return (
    <div className="App">
      <div className="table_holder">
        <div className="table_wrapper">
          <div className="clist">
            {role === "moneybag" ? (
              <center>
                <button onClick={newContractHandler}>New contract</button>
              </center>
            ) : (
              <div></div>
            )}
            <ContractsTable />
          </div>
        </div>
      </div>
    </div>
  );
}

const ContractsTable = () => {
  const [isLogin] = React.useState(window.localStorage.getItem("ACCESS"));
  const [contractsList, setContractsList] = React.useState([]);

  React.useEffect(() => {
    const all_contracts = [];
    SendRQ("contracts/", {
      headers: {
        "Content-Type": "Application/json",
      },
    }).then((data) => {
      data.forEach((contract) => {
        all_contracts.push(contract);
      });

      setContractsList(all_contracts);
    });
  }, []);

  return (
    <table>
      <thead>
        <tr>
          <th>название</th>
          <th>задание</th>
          <th>заказчик</th>
          <th>исполнитель</th>
          <th>цена</th>
          <th>статус</th>
        </tr>
      </thead>
      <tbody>
        {contractsList.map((contract) => (
          <tr key={contract.id}>
            <td>{contract.title}</td>
            <td>{contract.task}</td>
            <td>{contract.customer.username}</td>
            <td>{contract.executor.username}</td>
            <td>{contract.cost}</td>
            <td>{contract.status}</td>
            {isLogin ? (
              <td>
                <NavLink
                  to={{
                    pathname: "/edit",
                    userProps: {
                      key: contract.id,
                    },
                  }}
                >
                  <button>Edit</button>
                </NavLink>
              </td>
            ) : (
              <div></div>
            )}
          </tr>
        ))}
      </tbody>
    </table>
  );
};
