import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import MainPage from "./MainPage";
import Header from "./components/header";
import Footer from "./components/footer";
import ContractsList from "./ContractsList";
import { LoginPage, Logout } from "./Login";
import RegPage from "./Register";
import EditContract from "./EditContract";

function App() {
  return (
    <>
      <Router>
        <Header />
        <Switch>
          <Route path="/main">
            <MainPage />
          </Route>
          <Route path="/contracts">
            <ContractsList />
          </Route>
          <Route path="/logout">
            <Logout />
          </Route>
          <Route path="/login">
            <LoginPage />
          </Route>
          <Route path="/register">
            <RegPage />
          </Route>
          <Route path="/edit">
            <EditContract />
          </Route>
          <Route path="/">
            <Redirect to="/main"></Redirect>
          </Route>
        </Switch>
      </Router>

      <Footer />
    </>
  );
}

export default App;
