import { NavLink } from "react-router-dom";
import React from "react";

export default function Header() {
  const [isLogin] = React.useState(window.localStorage.getItem("ACCESS"));

  return (
    <header>
      <div className="bar">
        <div className="logo">
          <div className="icon"></div>
          <div className="text">SYANS</div>
        </div>

        <NavLink
          to="/main"
          className={(isActive) => (isActive ? "active" : "inactive")}
        >
          <div className="text">Главная</div>
          <div className="underline"></div>
        </NavLink>

        {isLogin ? (
          <NavLink
            to="/contracts"
            className={(isActive) => (isActive ? "active" : "inactive")}
          >
            <div className="join_btn">
              <div className="text">Задачи</div>
            </div>
          </NavLink>
        ) : (
          <div></div>
        )}

        {isLogin ? (
          <NavLink
            to="/logout"
            className={(isActive) => (isActive ? "active" : "inactive")}
          >
            <div className="login_btn">
              <div className="icon"></div>
              <div className="text">Выход</div>
            </div>
          </NavLink>
        ) : (
          <NavLink
            to="/register"
            className={(isActive) => (isActive ? "active" : "inactive")}
          >
            <div className="login_btn">
              <div className="icon"></div>
              <div className="text">Вход / Регистрация</div>
            </div>
          </NavLink>
        )}
      </div>
    </header>
  );
}
