import { NavLink } from "react-router-dom";
import React from "react";
import SendRQ from "./services/ApiClientServer";

export default function RegPage() {
  const [login, setLogin] = React.useState("");
  const [mail, setEmail] = React.useState("");
  const [pass, setPass] = React.useState("");
  const [pass2, setPass2] = React.useState("");
  const [err_msg, setError] = React.useState("");

  const handleRegister = async (event) => {
    event.preventDefault();
    setError("");

    let role = document.getElementById("select_role").value;

    if (login === "") {
      setError("Login must not be empty");
    } else if (mail === "") {
      setError("Email must not be empty");
    } else if (pass === "") {
      setError("Password must not be empty");
    } else if (pass2 === "") {
      setError("Confirm your password");
    } else if (pass !== pass2) {
      setError("Password mismatch");
    } else {
      await SendRQ("regme/", {
        headers: {
          "Content-Type": "Application/json",
        },
        method: "POST",
        body: JSON.stringify({
          username: login,
          email: mail,
          password: pass,
          last_name: role,
        }),
      });

      document.location.href = "/login";
    }
  };

  return (
    <>
      <div className="App">
        <div className="table_holder">
          <div className="table_wrapper">
            <div className="error_msg">{err_msg}</div>
            <table>
              <tbody>
                <tr>
                  <td align="right">
                    <label>login:</label>
                  </td>
                  <td>
                    <input
                      type="text"
                      value={login}
                      onChange={(event) => {
                        setLogin(event.target.value);
                      }}
                    ></input>
                  </td>
                </tr>
                <tr>
                  <td align="right">
                    <label>mail:</label>
                  </td>
                  <td>
                    <input
                      type="email"
                      value={mail}
                      onChange={(event) => {
                        setEmail(event.target.value);
                      }}
                    ></input>
                  </td>
                </tr>
                <tr>
                  <td align="right">
                    <label>password:</label>
                  </td>
                  <td>
                    <input
                      type="password"
                      value={pass}
                      onChange={(event) => {
                        setPass(event.target.value);
                      }}
                    ></input>
                  </td>
                </tr>
                <tr>
                  <td align="right">
                    <label>password (again):</label>
                  </td>
                  <td>
                    <input
                      type="password"
                      value={pass2}
                      onChange={(event) => {
                        setPass2(event.target.value);
                      }}
                    ></input>
                  </td>
                </tr>
                <tr>
                  <td align="right">
                    <label>Role:</label>
                  </td>
                  <td>
                    <select id="select_role">
                      <option value="blogger">blogger</option>
                      <option value="moneybag">moneybag</option>
                    </select>
                  </td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <button onClick={handleRegister}>Зарегестрироваться</button>
                  </td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <NavLink to="/login">
                      <button>Я уже смешарик</button>
                    </NavLink>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}
