import "./App.js";
import React, { useEffect } from "react";
import SendRQ from "./services/ApiClientServer.js";
import { NavLink } from "react-router-dom";
import { useLocation } from "react-router-dom";

export default function EditContract() {
  const [role, setRole] = React.useState("blogger");
  const [name, setName] = React.useState("");
  const [task, setTask] = React.useState("");
  const [cost, setCost] = React.useState(0);

  let key = useLocation().userProps.key;

  useEffect(() => {
    SendRQ(
      "contracts/?" +
        new URLSearchParams({
          id: key,
        }),
      {
        headers: {
          "Content-Type": "Application/json",
        },
        method: "GET",
      }
    ).then((data) => {
      setName(data[0].title);
      setTask(data[0].task);
      setCost(data[0].cost);
    });

    SendRQ("whoami/", {
      headers: {
        "Content-Type": "Application/json",
      },
      method: "GET",
    }).then((data) => {
      setRole(data.role);
    });
  }, []);

  const saveMBBtnHandler = async (event) => {
    let executor = document.getElementById("selector").value;

    SendRQ("contracts/", {
      headers: {
        "Content-Type": "Application/json",
      },
      method: "POST",
      body: JSON.stringify({
        id: key,
        title: name,
        task: task,
        cost: cost,
        executor: executor,
      }),
    }).then((resp) => {
      document.location.href = "contracts/";
    });
  };

  const saveBLBtnHandler = async (event) => {
    let status = document.getElementById("selector").value;
    SendRQ("contracts/", {
      headers: {
        "Content-Type": "Application/json",
      },
      method: "POST",
      body: JSON.stringify({
        id: key,
        status: status,
      }),
    }).then((resp) => {
      document.location.href = "contracts/";
    });
  };

  return (
    <div className="App">
      <div className="table_holder">
        <div className="table_wrapper">
          <div className="clist">
            <table>
              {role === "blogger" ? (
                <tbody>
                  <tr>
                    <td align="right">Статус</td>
                    <td>
                      <select id="selector">
                        <option>offered</option>
                        <option>denied</option>
                        <option>in process</option>
                        <option>done</option>
                      </select>
                    </td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <button onClick={saveBLBtnHandler}>Сохранить</button>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <tbody>
                  <tr>
                    <td align="right">
                      <label>Название</label>
                    </td>
                    <td>
                      <input
                        value={name}
                        onChange={(event) => {
                          setName(event.target.value);
                        }}
                      ></input>
                    </td>
                  </tr>
                  <tr>
                    <td align="right">
                      <label>Задание</label>
                    </td>
                    <td>
                      <input
                        value={task}
                        onChange={(event) => {
                          setTask(event.target.value);
                        }}
                      ></input>
                    </td>
                  </tr>
                  <tr>
                    <td align="right">
                      <label>Исполнитель</label>
                    </td>
                    <td>
                      <select id="selector">
                        <ExecutorsList task={task} />
                      </select>
                    </td>
                  </tr>
                  <tr>
                    <td align="right">
                      <label>Оплата</label>
                    </td>
                    <td>
                      <input
                        value={cost}
                        onChange={(event) => {
                          setCost(event.target.value);
                        }}
                      ></input>
                    </td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <button onClick={saveMBBtnHandler}>Сохранить</button>
                    </td>
                  </tr>
                </tbody>
              )}
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

const ExecutorsList = (props) => {
  const { task } = props;
  const [data, setData] = React.useState([]);

  useEffect(() => {
    async function fetchit() {
      const data = await SendRQ("executors/");
      setData(data);
      data.map((user) => {
        console.log(user.username);
      });
    }
    fetchit();
  }, []);

  return (
    <>
      {data.map((user) => (
        <option value={user.username}>{user.username}</option>
      ))}
    </>
  );
};
